import React, { useEffect } from "react";

const Pagination = ({ postsPerPage, totalPosts, paginate }) => {
  const pageNumbers = [];

  for (let i = 1; i <= Math.ceil(totalPosts / postsPerPage); i++) {
    pageNumbers.push(i);
  }

  useEffect(() => {
    let firstLiEl = document.querySelector(".page-item:first-child");
    firstLiEl.classList.add("active");
  }, []);

  const styleLi = (index) => {
    let allLis = document.querySelectorAll(".page-item");
    let nodeListArr = Array.from(allLis);

    for (let i = 0; i < nodeListArr.length; i++) {
      if (
        nodeListArr[i].classList.contains("active") &&
        index !== nodeListArr.indexOf(nodeListArr[i])
      )
        nodeListArr[i].classList.remove("active");
      if (index === nodeListArr.indexOf(nodeListArr[i])) {
        allLis[i].classList.add("active");
      }
    }
  };

  return (
    <nav>
      <ul id="pagination" className="pagination">
        {pageNumbers.map((number, index) => (
          <li key={number} className="page-item">
            <a
              onClick={() => {
                paginate(number);
                styleLi(index);
              }}
              href="#/"
              className="page-link"
            >
              {number}
            </a>
          </li>
        ))}
      </ul>
    </nav>
  );
};

export default Pagination;
