import React from "react";

import NavMenu from "./navMenu";
import Logo from "../assets/images/logo.png";
import { useLockBodyScroll } from "react-use";
import { Fade } from "hamburger-react";

const Header = ({ 
  viewportWidth, 
  showNavBtn,
  isHamburgerOpen,
  setIsHamburgerOpen,
  openNavMenu,
  setOpenNavMenu,
  closeNavMenu
}) => {
  // to prevent vertical scroll when the hamburger nav menu is open
  useLockBodyScroll(openNavMenu);

  return (
    <header>
      <div 
      className="logo" 
      style={{display: `${openNavMenu ? 'none' : 'block'}` }}
      >
        <a href="/">
          <img src={Logo} alt="logo" />
        </a>
      </div>
      {showNavBtn && (
        <Fade
          label="Show hamburger nav menu"
          toggled={isHamburgerOpen}
          toggle={setIsHamburgerOpen}
          onToggle={() => setOpenNavMenu(!openNavMenu)}
        />
      )}
      {viewportWidth >= 436 ? (
        <nav className="">
          <a href="#bio">Bio</a>
          <a href="#media">Videos</a>
          <a href="#music">Music</a>
          <a href="#tour">Tour</a>
          {/* <a href="/">Shop</a> */}
        </nav>
      ) : openNavMenu ? (
        <NavMenu
          openNavMenu={openNavMenu}
          setIsHamburgerOpen={setIsHamburgerOpen}
          closeNavMenu={closeNavMenu}
        />
      ) : null}
    </header>
  );
};

export default Header;
